/*Общие стили*/

* {
  box-sizing: border-box;
}

html {
  overflow-x: hidden;
}

body {
  margin: 0;
  padding: 0;
  min-width: 300px;
  position: relative;
}

.wrapper {
  max-width: 1230px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.wrap {
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
  font-size: 0;
}

.ears::after {
  content: '';
  display: block;
  height: 100%;
  width: 100vw;
  background-color: inherit;
  position: absolute;
  top: 0;
  z-index: -1;
  margin: auto;
  left: -50vw;
  right: -50vw;
}

img {
  max-width: 100%;
  height: auto;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
  cursor: pointer;
  outline: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

.d-none {
  display: none;
}

.clearfix::after {
  content: "";
  display: table;
  clear: both;
}

/*----*/

/*---------------------------preloader--------------------------------*/

.preloader {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 999999;
  background: #090c34;
  transition: 1s all;
  opacity: 1;
  visibility: visible;
}

.preloader.done {
  opacity: 0;
  visibility: hidden;
}

.preloader__spinner {
  width: 150px;
  height: 150px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.preloader__percents {
  font-size: 25px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #FDBA0B;
}

.preloader__animation {
  width: 150px;
  height: 150px;
  position: relative;
  animation: spin 7s linear infinite;
}

.preloader__animation,
.preloader__animation:before,
.preloader__animation:after {
  content: ' ';
  display: block;
  position: absolute;
  border: 3px solid transparent;
  border-top-color: #FDBA0B;
  border-radius: 50%;
}

.preloader__animation:before {
  top: 13px;
  left: 13px;
  right: 13px;
  bottom: 13px;
  border-top-color: #C22229;
  animation: spin 3.5s linear infinite;
}

.preloader__animation:after {
  top: 5px;
  left: 5px;
  right: 5px;
  bottom: 5px;
  border-top-color: #006A42;
  animation: spin 6s linear infinite;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@media screen and (max-width: 920px) {
  .wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }
}

@media screen and (max-width: 576px) {
  .wrapper {
    padding-left: 0px;
    padding-right: 0px;
  }
}

@media screen and (max-width: 480px) {
  .wrapper {
    padding-left: 0px;
    padding-right: 0px;
  }
}